import React from 'react'
import Logo from './Logo'

export default function Welcome() {
    const production = true
    var twitchLoginUrl = 'https://id.twitch.tv/oauth2/authorize?response_type=code&client_id=fshwwwtjkuk4x4xda6wiz4mcfenicw&scope=channel:read:redemptions+user:read:email&redirect_uri='
    twitchLoginUrl += production ? 'https://robot-ape.herokuapp.com/options' : 'http://localhost:3002'
    return (
        <div className='welcomeDiv'>
            <Logo />
            <div className='aboutRobotDiv'>
                <h1 className='aboutRobotApe'>Robot Ape is a Twitch chatbot with exclusive commands and channel points rewards.</h1>
            </div>

            <a href={twitchLoginUrl}><button className='signInButton'>Sign in with Twitch.</button></a>
        </div>
    )
}
